<template>
	<div class="row my-0 gy-2 gx-2">
		<div class="col">
			<div v-show="Object.keys(GET_USER).length > 0" class="padding colored rounded section">
				<h5>Профиль</h5>
				<div class="offset"></div>
				<div class="info black">
					<p><strong>{{ GET_USER.display_name }}</strong></p>
					<p>{{ GET_USER.phone }}</p>
				</div>
				<div class="offset"></div>
				<button class="btn white" @click="logoutToMain">Выход</button>
			</div>
		</div>
		<div class="col-md-6 ml-auto">
			<div class="row gx-0 padding colored rounded-top section">
				<div class="col">
					<h5>Мои объявления</h5>
				</div>
				<div class="col-auto ">
					<button class="btn blue" @click="goToCreateListing">
						<span class="material-icons-round">add</span>
						Добавить
					</button>
				</div>
			</div>

			<div class="colored rounded-bottom">
				<Listings :listings="GET_LISTINGS" @on-item-click="onItemClick"/>
			</div>
		</div>
	</div>
</template>

<script>
import Listings from '@/components/Listings'
import { mapGetters, mapActions } from 'vuex'

export default {
	name: 'MyListings',
	computed: mapGetters(['GET_LISTINGS', 'GET_USER']),
	methods: {
		...mapActions(['getListings', 'fetchUser', 'logout']),
		logoutToMain() {
			this.logout()
			this.$router.push({name : 'Home'})
		},
		onItemClick(id) {
			this.$router.push({name : `/item/${id}`})
			console.log(id);
		},
		goToCreateListing() {
			this.$router.push({name : 'CreateListing'})
		}
	},
	async created() {
		this.fetchUser()
		this.getListings()
	},
	components: {
		Listings
	}
}
</script>

