<template>
  <div class="container">
    <div class="login">
      <form>
        <h2>Авторизация</h2>
        <div class="input">
          <p>Номер телефона</p>
          <input type="text" v-model="login" placeholder="+7">
        </div>
        <div class="input">
          <p>Пароль</p>
          <input type="password" v-model="password" placeholder="Пароль">
        </div>
        <div class="input-forgot-password">
          <a>Забыли пароль?</a>
        </div>
        <div class="input">
          <button @click="signIn" >Вход</button>
        </div>
        <div class="input-create-account">
          <router-link :to="{ name : 'Register' }">
            <a>Создать аккаунт</a>
          </router-link>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginView',
  data() {
    return {
      login: '',
      password: '',
      session_url: 'login'
    }
  },
  methods: {
    async signIn() {
      const auth = {
        username: this.login,
        password: this.password
      }
      this.$store.dispatch('login', auth).then(() => {
        this.$router.replace({ name : 'MyListings'})
      }).catch(error => {
        console.log(error);
      })
      console.log(auth);
    }
  },

}
</script>

