<template>
		<div class="padding listing" v-for="listing in listings" :key="listing.id">
			<div class="row no-gutters">
				<div class="col">
					<h6 @click="$emit('on-item-click')">{{ listing.product.name + ', ' + listing.storage.value + ' GB, ' + listing.color.value }}</h6>
					<div class="offset-s"></div>
					<div class="info">
						<span class="material-icons-round">description</span>
						<p>{{ listing.description }}</p>
					</div>
					<div class="offset-s"></div>
					<div class="row gx-1 gy-1 tags">
						<div class="col-auto">
							<div class="item">
								<p>{{ '$' + listing.price }}</p>
							</div>
						</div>
						<div class="col-auto">
							<div class="item" :style="{ background: getCondition(listing.condition_state.id).color }">
								<span class="material-icons-round">perm_device_information</span>
								<p>{{ listing.condition_state.value }}</p>
							</div>
						</div>
						<div class="col-auto">
							<div class="item">
								<span class="material-icons-round">battery_std</span>
								<p>{{ listing.battery_health + '%' }}</p>
							</div>
						</div>
					</div>

				</div>
			</div>
		</div>
</template>

<script>
import conditionDecoder from '@/services/condition-decoder'

export default {
	name: 'Listings',
	mixins: [conditionDecoder],
	props: {
		listings: {
			type: Array
		}
	},
	emits: ['on-item-click'],
}
</script>

<style scoped>

</style>