<template>
  <div class="container">
    <div class="login">
      <form>
        <h2>Регистрация</h2>
        <div class="input">
          <p>Номер телефона</p>
          <input type="text" v-model="registerData.phone" placeholder="7XXXXXXXXXX">
        </div>
        <div class="input">
          <p>Как Вас зовут?</p>
          <input type="text" v-model="registerData.display_name" placeholder="Ваше имя">
        </div>
        <div class="input">
          <p>Придумайте пароль</p>
          <input type="password" v-model="registerData.password" placeholder="Пароль">
        </div>
        <div class="input">
          <button @click="onClick">Создать аккаунт</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>

export default {
  name: 'RegisterView',
  data() {
    return {
      registerData: {
        phone: '',
        display_name: '',
        password: '',
      }
    }
  },
  methods: {
    async onClick() {
      this.$store.dispatch('register', this.registerData).then(() => {
        console.log('log in');
          this.$router.replace({ name : 'Login'})
      })
    
    }
  },

}
</script>

