export default {
  methods: {
    getCondition: (code) => {
      switch(code) {
				case 1:
					return { color : '#84FFFF', text : 'Новый'}
				case 2:
					return { color : '#A7FFEB', text : 'Хороший'}
        case 3:
					return { color : '#FFE57F', text : 'Удовлетворительный'}
        default:
          return { color : '#A7FFEB', text : 'Хороший'}
			}
    }
  }
}