import axionInstance from '@/axios-instance'

export default {
  actions: {
    async getListings({ commit }) {
      return axionInstance({
        method: 'get',
        url: 'listings'
      }).then(response => {
        console.log(response.data.listings);
        commit('SET_LISTINGS', response.data.listings)
        return Promise.resolve(response)
      }).catch(error => {
        console.log(error);
        commit('SET_LISTINGS', [])
        return Promise.reject(error)
      })
    },
    async createListing(_, listingData) {
      return axionInstance({
        method: 'post',
        url: 'listings',
        data: listingData
      }).then(response => {
        console.log(response.data.listings);
        return Promise.resolve(response)
      }).catch(error => {
        console.log(error);
        return Promise.reject(error)
      })
    }
  },
  mutations: {
    SET_LISTINGS: (state, listings) => {
      state.listings = listings
    }
  },
  state: {
    listings: []
  },
  getters: {
    GET_LISTINGS:(state) => state.listings
  }
}